import { useState, useEffect } from 'react';
import { useParams } from "react-router-dom";
import BasicUserItem from '../components/BasicUserItem';

const BasicUserList = () => {

  const [users, setUsers] = useState([]);
  const { id } = useParams();

  useEffect(() => {
    setUsers([
      {id:1, name: 'AAA', gender_name: '', category_name: ''},
      {id:1, name: 'BBB', gender_name: '', category_name: ''},
      {id:1, name: 'CCC', gender_name: '', category_name: ''},
    ])
  }, [])

  return (
    <>
    {users.map(e => {
      return (
        <BasicUserItem user={e} />
      )
    })}
    </>
  )

}

export default BasicUserList;