import { useState, useEffect } from 'react';
import UserItem from '../components/UserItem';

const UserItemList = () => {

  const [users, setUsers] = useState([]);

  useEffect(() => {
    setUsers([
      {id:1, name: 'AAA', gender_name: '', category_name: ''},
      {id:1, name: 'BBB', gender_name: '', category_name: ''},
      {id:1, name: 'CCC', gender_name: '', category_name: ''},
    ])
  }, [])
  return (
    <>
     {users.map(e => {
      return (
        <UserItem user={e} />
      )
    })}
    </>
  )
}

export default UserItemList;