import Container from '@mui/material/Container';
import EventItem from '../components/EventItem';
import { useState, useEffect } from 'react';
import Checkbox from '@mui/material/Checkbox';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import Typography from '@mui/material/Typography';

const BulkReservation = () => {

  const [year, setYear] = useState(new Date().getFullYear());
  const [month, setMonth] = useState(new Date().getMonth());

  const [events, setEvents] = useState([]);
  useEffect(() => {
    setEvents([
      { id: 1, day: 1, short_name: '', event_name: '', status: 9 },
      { id: 2, day: 1, short_name: '', event_name: '', status: 9 },
      { id: 3, day: 2, short_name: '', event_name: '', status: 1 },
      { id: 4, day: 3, short_name: '', event_name: '', status: 2 },
      { id: 5, day: 3, short_name: '', event_name: '', status: 3 },
      { id: 6, day: 4, short_name: '', event_name: '', status: 4 },
      { id: 7, day: 4, short_name: '', event_name: '', status: 5 },
    ])
  }, [year, month])

  return (
    <Container>
      <Grid container spacing={5} marginTop={5}>
      <Grid item xs={1}>
        <ArrowBackIosIcon
          cursor={'pointer'}
          onClick={() => {
            setYear(month === 0 ? year - 1 : year);
            setMonth(month === 0 ? 11 : month - 1);
          }}
        />
      </Grid>
      <Grid item xs={1}>
        <ArrowForwardIosIcon
          cursor={'pointer'}
          onClick={() => {
            setYear(month === 11 ? year + 1 : year);
            setMonth(month === 11 ? 0 : month + 1);
          }}
        />
      </Grid>
      <Grid item xs={4}>
        <Typography variant="h5" textAlign={'right'}>{year}</Typography>
      </Grid>
      <Grid item xs={4}>
        <Typography variant="h5" textAlign={'left'}>{month + 1}</Typography>
      </Grid>
      <Grid item xs={2}/>
      {events.map(e => {
        return (
          <>
          <Grid item xs={1}>
            <Checkbox 
              sx={{m:5}}
            />
          </Grid>
          <Grid item xs={11}>
            <EventItem
              eventInfo={e}
            />
          </Grid>
          </>
        )
      })}
      <Grid item xs={12}>
        <Button
          variant="contained"
          sx={{marginLeft:20,
            marginBottom:10,
          }}
        >
          
        </Button>
      </Grid>
      </Grid>
    </Container>
  )

}

export default BulkReservation;